import { useCallback } from "react";
import { MdStar, MdStarBorder } from "react-icons/md";
import styled from "styled-components";
import { LinkButton } from "./primitives/LinkButton";

export const StyledStarButton = styled(LinkButton)`
  font-size: 1.5rem;
  line-height: 1;
  color: #ffeb3b;
`;

export interface Props {
  isStarred: boolean;
  setIsStarred: (isStarred: boolean) => void;
}

export const StarButton: React.FC<Props> = ({
  isStarred,
  setIsStarred,
  ...props
}) => {
  const toggle = useCallback(() => setIsStarred(!isStarred), [
    isStarred,
    setIsStarred,
  ]);
  return (
    <StyledStarButton
      aria-label="Star"
      aria-pressed={isStarred}
      onClick={toggle}
      {...props}
    >
      {isStarred ? <MdStar /> : <MdStarBorder />}
    </StyledStarButton>
  );
};
