import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import { App } from "./App";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";
import reportWebVitals from "./reportWebVitals";
import { SWRProvider } from "./config/swr";
import { BrowserRouter as Router } from "react-router-dom";
import { StarredProvider } from "./contexts/starred";
import { MealsProvider } from "./contexts/meals";
import { FdcApiKeyProvider } from "./contexts/fdcApiKey";
import { AlertProvider } from "./contexts/alerts";

const basename = process.env.NODE_ENV === "production" ? "/healthy" : "";

ReactDOM.render(
  <React.StrictMode>
    <SWRProvider>
      <AlertProvider>
        <FdcApiKeyProvider>
          <StarredProvider>
            <MealsProvider>
              <Router basename={basename}>
                <App />
              </Router>
            </MealsProvider>
          </StarredProvider>
        </FdcApiKeyProvider>
      </AlertProvider>
    </SWRProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
